
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import api from '../../fetch/api'
import HlCell from '../../base/hl-cell/hl-cell'
import wx from 'weixin-js-sdk'
import { isWeiXin } from '../../util/util'
import { mapGetters, mapMutations } from 'vuex'

export default {
props: {
pagState: {
type: String,
default: ''
},
shopName: {
type: String,
default: '3'
},
name: {
type: String,
default: ''
},
address: {
type: String,
default: ''
},
orderTime: {
type: String,
default: '455'
},
orderNo: {
type: String,
default: ''
}
},
data () {
return {
detaileContan: '',
orderGuid: '',
orderType: 9,
qrcodeImg: '',
expiresIn: 0,
timer: null,
itemList: {},
vShopProductType: 0,
vShopProductGuid: ''
}
},
created () {

},
activated () {
this.signApi()
this.getOrder()
this.getQrcode()
},
mounted () {
this.signApi()


},
methods: {

getOrder () {

api.orderDetails({

vOrderGuid: this.$route.query.vOrderGuid
}).then(res => {
console.log('')
console.log(res)
if (res.code === 0) {
this.itemList = res.data
this.vShopProductGuid = res.data.vShopProductGuid
this.signApiShare()
}
}, err => {
this.$msgbox('', err.data.message)
})
},
doPay (orderGuid) {
let that = this
isWeiXin(function () {
that.toPay(orderGuid)
}, function () {
let options2 = {
'orderType': that.orderType,
'payway': 2,
'orderGuid': orderGuid
}
let jsonStr = JSON.stringify(options2)
that.anzhuoPay(jsonStr)
})
},
anzhuoPay (jsonStr) {

this.$msgbox('', '')

},
orderTimeFn (timeFn) {
return '' + timeFn
},
orderNoFn (orderNo) {
return '' + orderNo
},
commedTotal (total) {
return '' + total + ''
},
totalMenoy (fellMenoy) {
return '' + fellMenoy
},
consumCode (feelNum) {
return ':' + feelNum
},
isPageState (orderStatus, itemCode) {
if (orderStatus === 1) {
return ''
} else if (orderStatus === 2) {
if (itemCode === 2) {
return ''
} else {
return ''
}
} else if (orderStatus === 3) {
return ''
} else if (orderStatus === 5) {
return ''
} else if (orderStatus === 6) {
return ''
} else if (orderStatus === 7) {
return ''
} else if (orderStatus === 8) {
return '()'
} else if (orderStatus === 9) {
return ''
}
},
canell (orderGuid) {

let than = this
this.$confirm('', '?', function () {
than.getCancelOder(orderGuid)
})
},
getCancelOder (orderGuid) {

api.cancelOrder({

vOrderGuid: orderGuid
}).then(res => {
let that = this
if (res.code === 0) {
this.$msgbox('', '', function () {
that.getOrder()

})
} else {
alert('')
}
}, err => {
this.$msgbox('', err.data.message)
})
},

confirmReceipt (orderGuid) {
let than = this

this.$confirm('', '?', function () {
than.getConfirmReceipt(orderGuid)

console.log(orderGuid)
})
},
/*  */
getQrcode () {


api.qrcode({

vOrderGuid: this.$route.query.vOrderGuid
}).then(res => {
if (res.code === 0) {
this.qrcodeImg = res.data.qrcodeUrl
this.expiresIn = res.data.expiresIn
this.settime(res.data.expiresIn)
}
}, err => {

console.log(err.data.message)
})
},
settime (time) {
let than = this
time--
if (time <= 0) {
than.getQrcode()
} else {
clearTimeout(than.timer)
than.timer = setTimeout(function () {
than.settime(time)

}, 1000)
}
},
intervalQrcode (fnc) {


api.qrcode({

vOrderGuid: this.$route.query.vOrderGuid
}).then(res => {
if (res.code === 0) {
this.qrcodeImg = res.data.qrcodeUrl
this.expiresIn = res.data.expiresIn
fnc && fnc(this.expiresIn)
}
}, err => {
this.$msgbox('', err.data.message)
})
},
getConfirmReceipt (orderGuid) {
api.confirm({

vOrderGuid: orderGuid
}).then(res => {
let that = this
if (res.code === 0) {
this.$msgbox('', '', function () {

that.getOrder()
})
} else {
console.log('')
}
}, err => {
this.$msgbox('', err.data.message)
})
},
toPay (orderGuid) {


api.doPay({

orderGuid: orderGuid,
orderType: this.orderType
}).then(res => {
console.log('')
console.log(res)
let options = res.data
this.wxPay(options)
}, err => {
console.log('')
this.$msgbox('', err.data.message)
console.log(err)
})
},
wxPay (option) {
let than = this
wx.chooseWXPay({

'paySign': option.sign,
'timestamp': option.timestamp,
'nonceStr': option.noncestr,
'package': option.packageValue,
'signType': option.signType,
'success': function (res) {
console.log('88')
console.log(res)

if (res.errMsg === 'chooseWXPay:ok') {

than.getOrder()
} else if (res.errMsg === 'chooseWXPay:cancel') {
alert('')
} else if (res.errMsg === 'chooseWXPay:fail') {
alert('')
}
}
})
},

signApi () {
let urlRe = encodeURIComponent(window.location.href)
api.sign({
url: urlRe
}).then(res => {
console.log('res')
console.log(res)
let config = res.data
wx.config({
debug: false,
appId: config.appId,                // 
timestamp: config.timestamp,        // 
nonceStr: config.nonceStr,          // 
signature: config.signature,        // 1
jsApiList: config.jsApiList                // JSJS2
})














}, err => {
console.log('err')
console.log(err)
})
},
signApiShare () {
let urlRe = encodeURIComponent(window.location.href)
let that = this
api.sign({
url: urlRe
}).then(res => {
console.log('res')
console.log(res)
let config = res.data
wx.config({
debug: false,
appId: config.appId,                // 
timestamp: config.timestamp,        // 
nonceStr: config.nonceStr,          // 
signature: config.signature,        // 1
jsApiList: config.jsApiList                // JSJS2
})
wx.ready(function () {
that.setShareProduct(function (res) {
let options = {
title: res.shareTitle,
desc: res.shareContent,
link: res.shareUrl,
imgUrl: res.shareLogo,
success: function () {
console.log('ok')
},
cancel: function () {
console.log('not_ok')
}
}
wx.onMenuShareTimeline(options)
wx.onMenuShareAppMessage(options)
})
})
}, err => {
console.log('err')
console.log(err)
})
},
setShareProduct (optionFn) {


api.shareProduct({
vShopProductGuid: this.vShopProductGuid
}).then(res => {
console.log('2')
console.log(res)
if (res.code === 0) {
optionFn && optionFn(res.data)
}
}, err => {
console.log('2')
console.log(err)
})
},
...mapMutations({
setvOrderGuid: 'SET_VORDERGUID'
})
},
computed: {
...mapGetters(['vOrderGuid'])
},
components: {
XHeader,
Group,
Cell,
XButton,
HlCell
},
watch: {
}
}



